___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
}

:root {
    --primary-color: #FFECD1;
    --secondary-color: #E9C46A;
    --text-color: #264653;
    --error-color: #FF0000;
    --active-color: #CFD1D8
}

body {
    font-family: sans-serif;
    background-color: var(--primary-color);
    color: var(--text-color);
}

.container {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
}

header {
    min-height: 70px;
    background-color: var(--secondary-color);
    display: flex;
    justify-content: end;
    align-items: center;
    padding: 10px 20px;
}

.inputBox {
    display: flex;
    align-items: center;
    border-width: 0 0 2px 0;
    border-style: solid;
}

.searchInput {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none;
    background-color: transparent;
    width: 200px;
    height: 28px;
    padding-left: 10px;
    border: 0px;
    color: var(--text-color);
}

.searchImg {
    width: 20px;
    height: 15px;
}

.errorInputBox{
   border-color: var(--error-color);
}

.errorInput{
    display: none;
    color: var(--error-color);
    margin-top: 10px;
    font-size: 10px;
}

.weatherBox {
    max-width: 400px;
    background-color: transparent;
    color: var(--text-color);
    opacity: 1;
    margin: 30px auto;
    padding: 10px;
    /* border: 1px solid green; */
}

.countryBox {
    text-align: center;
    font-size: 1.5em;
    padding: 10px;
}

.temperatureBox {
    display: flex;
    justify-content: center;
    flex-direction: row;
}

.temperatureContent {
    display: flex;
    font-size: 8em;
    text-align: center;
}

.temperatureTypeBox {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.weatherTypeBox {
    text-align: center;
    padding: 10px;
}

.weatherTypeBox p {
    font-weight: bold;
}

.weatherDetailsBox {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

#weatherImg {
    margin: 15px 0;
}

.detailBox {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 7px 0;
}

.detailImg {
    width: 25px;
    height: 25px;
    margin: 0 5px;
}

button {
    background-color: transparent;
    border: 0;
    color: var(--text-color);
    font-size: 2em;
    padding: 0 10px;
}

button:hover {
    cursor: pointer;
}

.active {
    border: 1px solid var(--secondary-color);
    border-radius: 2px;
}

.weatherFiveDaysContainer {
    max-width: 800px;
    margin: 60px auto;
    padding: 10px;
    background: var(--secondary-color);
    border-radius: 5px;
}

.weatherFiveDaysHeader {
    margin: 10px 0 20px 0;
}

.weatherFiveDaysCards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    grid-auto-flow: column;
    grid-auto-columns: minmax(150px, 160px);
    overflow-x: auto;
}

.weatherCard {
    text-align: center;
}


.weatherCardTemp {
    display: flex;
    justify-content: center;
    margin: 10px;
}

@media only screen and (max-width: 480px) {
    header {
        flex-direction: column;
        justify-content: center;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;AACd;;AAEA;IACI,wBAAwB;IACxB,0BAA0B;IAC1B,qBAAqB;IACrB,sBAAsB;IACtB;AACJ;;AAEA;IACI,uBAAuB;IACvB,sCAAsC;IACtC,wBAAwB;AAC5B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,gBAAgB;IAChB,wCAAwC;IACxC,aAAa;IACb,oBAAoB;IACpB,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,wBAAwB;IACxB,qBAAqB;IACrB,gBAAgB;IAChB,aAAa;IACb,6BAA6B;IAC7B,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,WAAW;IACX,wBAAwB;AAC5B;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;GACG,gCAAgC;AACnC;;AAEA;IACI,aAAa;IACb,yBAAyB;IACzB,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,gBAAgB;IAChB,6BAA6B;IAC7B,wBAAwB;IACxB,UAAU;IACV,iBAAiB;IACjB,aAAa;IACb,6BAA6B;AACjC;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,cAAc;IACd,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,uBAAuB;AAC3B;;AAEA;IACI,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,qCAAqC;AACzC;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,mBAAmB;IACnB,cAAc;AAClB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,6BAA6B;IAC7B,SAAS;IACT,wBAAwB;IACxB,cAAc;IACd,eAAe;AACnB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,wCAAwC;IACxC,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;IAChB,iBAAiB;IACjB,aAAa;IACb,kCAAkC;IAClC,kBAAkB;AACtB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,4DAA4D;IAC5D,sBAAsB;IACtB,uCAAuC;IACvC,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;AACtB;;;AAGA;IACI,aAAa;IACb,uBAAuB;IACvB,YAAY;AAChB;;AAEA;IACI;QACI,sBAAsB;QACtB,uBAAuB;IAC3B;AACJ","sourcesContent":["* {\n    margin: 0;\n    padding: 0;\n}\n\n:root {\n    --primary-color: #FFECD1;\n    --secondary-color: #E9C46A;\n    --text-color: #264653;\n    --error-color: #FF0000;\n    --active-color: #CFD1D8\n}\n\nbody {\n    font-family: sans-serif;\n    background-color: var(--primary-color);\n    color: var(--text-color);\n}\n\n.container {\n    display: flex;\n    flex-direct